/**
 * 
 */
import { chat, getSummarySystemPrompt } from './aiService'

export interface ArticleSummary {
  /**  */
  overview: string
  /**  */
  keyPoints: string[]
  /**  */
  targetAudience: string
  /**  */
  readingTime: string
}

/**
 *  AI 
 */
function parseSummary(content: string): ArticleSummary {
  const lines = content.split('\n').filter(line => line.trim())

  let overview = ''
  const keyPoints: string[] = []
  let targetAudience = ''
  let readingTime = ''

  let inKeyPoints = false

  for (const line of lines) {
    const trimmedLine = line.trim()

    // """"
    if (
      !overview &&
      (trimmedLine.includes('') ||
        trimmedLine.includes('') ||
        lines.indexOf(line) === 0)
    ) {
      overview = trimmedLine.replace(/^[^:]+[:]\s*/, '').trim()
      continue
    }

    // 
    if (
      trimmedLine.includes('') ||
      trimmedLine.includes('') ||
      trimmedLine.includes('')
    ) {
      inKeyPoints = true
      continue
    }

    //  - 
    if (
      inKeyPoints &&
      (trimmedLine.startsWith('-') ||
        trimmedLine.startsWith('') ||
        /^\d+[.]/.test(trimmedLine))
    ) {
      const point = trimmedLine.replace(/^[-\d.\s]+/, '').trim()
      if (point && keyPoints.length < 5) {
        keyPoints.push(point)
      }
      continue
    }

    // 
    if (
      trimmedLine.includes('') ||
      trimmedLine.includes('') ||
      trimmedLine.includes('')
    ) {
      targetAudience = trimmedLine.replace(/^[^:]+[:]\s*/, '').trim()
      inKeyPoints = false
      continue
    }

    // 
    if (
      trimmedLine.includes('') ||
      trimmedLine.includes('') ||
      trimmedLine.includes('')
    ) {
      readingTime = trimmedLine.replace(/^[^:]+[:]\s*/, '').trim()
      continue
    }
  }

  // 
  if (!overview && lines.length > 0) {
    overview = lines[0].replace(/^[^:]+[:]\s*/, '').trim()
  }

  // 
  if (!targetAudience) {
    targetAudience = ''
  }

  // 
  if (!readingTime) {
    readingTime = ''
  }

  return {
    overview,
    keyPoints: keyPoints.length > 0 ? keyPoints : [''],
    targetAudience,
    readingTime,
  }
}

/**
 * 
 */
export async function generateSummary(
  articleTitle: string,
  articleContent: string
): Promise<ArticleSummary> {
  const systemPrompt = getSummarySystemPrompt()

  // 3000
  const contentToAnalyze = articleContent.slice(0, 3000)

  const userPrompt = `

${articleTitle}


${contentToAnalyze}

`

  try {
    const response = await chat({
      messages: [
        { role: 'system', content: systemPrompt },
        { role: 'user', content: userPrompt },
      ],
      temperature: 0.3,
      maxTokens: 1000,
    })

    return parseSummary(response.content)
  } catch (error) {
    console.error(':', error)
    throw error
  }
}

/**
 * 
 */
export async function generateTagSuggestions(
  articleTitle: string,
  articleContent: string,
  existingTags: string[]
): Promise<string[]> {
  const prompt = ` 3-5 

${articleTitle}


${articleContent.slice(0, 1500)}

${existingTags.join(', ')}


1. 10
2. 
3. 
4. 

React, TypeScript, `

  try {
    const response = await chat({
      messages: [{ role: 'user', content: prompt }],
      temperature: 0.5,
      maxTokens: 100,
    })

    const tags = response.content
      .split(/[,]/)
      .map(tag => tag.trim())
      .filter(tag => tag && tag.length <= 10)
      .slice(0, 5)

    return tags
  } catch (error) {
    console.error(':', error)
    return []
  }
}

/**
 * 
 */
export async function generateRelatedQuestions(
  articleTitle: string,
  articleContent: string
): Promise<string[]> {
  const prompt = ` 3 

${articleTitle}


${articleContent.slice(0, 1500)}


1. 
2. ""
3. 
4. `

  try {
    const response = await chat({
      messages: [{ role: 'user', content: prompt }],
      temperature: 0.7,
      maxTokens: 200,
    })

    const questions = response.content
      .split('\n')
      .map(q => q.trim())
      .filter(q => q && q.endsWith('?'))
      .slice(0, 3)

    return questions
  } catch (error) {
    console.error(':', error)
    return []
  }
}
